.form-header {
  line-height: 24px;
  color: var(--main-text-color);
  font-size: 16px;
  font-weight: 500;
  margin: 10px 16px 6px; }

.input {
  border: 1px solid var(--form-border-color);
  padding: 3px 12px;
  box-sizing: border-box; }

.form {
  --adm-font-size-9: 14px;
   :global(.adm-list-item-content-main) {
    padding: 4px 0; }
   :global(.adm-form-item.adm-form-item-horizontal .adm-list-item-content-prefix) {
    padding-top: 4px;
    padding-bottom: 4px; }
   :global(.adm-form-item-label) {
    padding-top: 3px; }
   :global(.adm-form-item-label .adm-form-item-required-asterisk) {
    left: auto;
    margin-left: 3px;
    margin-top: 3px; }
   :global(.adm-list-default .adm-list-body) {
    border-bottom: none; } }
.divider {
  margin: 10px 16px 0; }

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid var(--main-border-color); }

.header-name-wrapper {
  display: flex;
  font-weight: 400;
  align-items: center;
  color: var(--main-text-color); }
.header-name {
  margin-left: 3px; }

.delete {
  color: var(--main-blue-color);
  font-weight: 400;
  display: flex;
  align-items: center;
  cursor: pointer; }

.delete-icon {
  width: 14px;
  height: 15px;
  margin-right: 3px;
  background: url('../../../assets/images/delete@2x.png') no-repeat center center;
  background-size: contain; }

.user-icon {
  width: 14px;
  height: 16px;
  background: url('../../../assets/images/user@2x.png') no-repeat center center;
  background-size: contain; }

.confirm-content {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 20px; }
